#blogpost-page
{
    font-size: 18px;


& .post-title
{
    font-size: 2.25em;
    font-weight: 700;
}



& .centre {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2em;
  margin-bottom: 2em;
  width: 50%;
}

& .centre-wide {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2em;
  margin-bottom: 2em;
  width: 75%;
}

& .imageshadow
{
    box-shadow: 10px 10px 5px grey;
}

& .article-header
{
    padding-top: 2em;
    padding-bottom: 1.5em;
    margin-bottom: 0.5em;
}


& .post-thumbnail
{
    width: 100px;
    height: 100px;
    float: left;
    margin-bottom: 0.5em;
    margin-right: 1.5em;

}

& .post-thumbnail img
{
    width: 100px;
    height: 100px;
}

& .post-rubric
{
    float: left;
}

& .post-meta
{
    margin-top: 1em;
    font-size: 0.9em;
}

& .description
{
    margin-top: 2em;
    font-size: 1.5em;
    font-weight: 300;
    color: grey;
    
}    

& .clear
{
    clear: both;

}

& .tags
{
    margin-top: 0.5em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    margin-bottom: 0.5em;
}

& .tagclass
{
    margin-right: 0.5em
}

& .article-post
{
    margin-bottom: 2em;
}

& .article-post p
{
    margin-bottom: 1em;
}

& .sidebyside
{
    float: left;
}

& .article-post blockquote
{
    margin-left: 4em;
    margin-right: 4em;
    font-family: "Times New Roman";
}

& .article-post blockquote p
{
    margin-bottom: 0.5em;
}

& .textlist
{
    background-color: lightgrey;
    font-family: monospace;
    font-size: 0.75em;
    list-style-type: none;
    padding: 1em;
    margin: 1em;
}



}
