#projarticle-page
{
    font-size: 16px;

/* The following styles are nested inside */

& .lecture-num
{
    font-size: 1em;
}

& .post-title
{
    font-size: 2.25em;
    font-weight: 700;
}

& .description
{
    margin-top: 1em;
    font-size: 1.5em;
    font-weight: 300;
    color: grey;
    
}  

& .article-header
{
    padding-top: 2em;
    padding-bottom: 1.5em;
    margin-bottom: 0.5em;
}

& h1
{
    font-size: 1.5em;
    font-weight: 700;
    border-bottom: 3px solid black;
    margin-top: 2em;
    margin-bottom: 1em;
}

& h2
{
    font-size: 1.25em;
    font-weight: 700;
    text-decoration-line: underline;
    margin-top: 1em;
    margin-bottom: 1em;
}

& h3
{
    font-size: 1.1em;
    font-weight: 700;
    margin-top: 1em;
    margin-bottom: 0.5em;

}

& h4
{
    font-size: 1em;
    font-weight: 500;
    text-decoration-line: underline;
    margin-top: 1em;
    margin-bottom: 0.5em;
}

& .highlighter-rouge      /* class name generated for pre tags   */
{
    background-color: lightgrey;
    padding: 0.5em;
    margin-bottom: 1em; 
}

& pre
{
    margin: 0em;
}

& code
{
    color: #000000;
}

& .col1
{
    
}

& table {
  width: 100%;
  margin-bottom: 1em;
}

& th.col1, th.col2
{
  width: 15%
}

th.col3 {
  width: 70%; /* Not necessary, since only 70% width remains */
}







}
